@import url(https://fonts.googleapis.com/css?family=Lato:400,700,700i,900,900i);
.header {
  background-color: #1d1e2b;
  display: flex;
  justify-content: space-around;
  padding: 10px 0;
  color: #babcc5; }
  .header .header_logo {
    width: 38px; }
  .header ul {
    display: flex;
    justify-content: center;
    list-style-type: none;
    margin-left: 150px; }
    .header ul li:not(:first-of-type) {
      border-left: 1px solid #babcc5; }
    .header ul li {
      padding: 0 20px; }

button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 300px;
  height: 45px;
  background-color: #ff7250;
  color: #ffffff;
  font-size: 16px;
  font-style: bold;
  border: 1px solid;
  border-radius: 10px;
  margin-top: 30px; }

.intro {
  display: flex;
  justify-content: space-around;
  margin-top: 50px; }

.main_image {
  min-width: 400px;
  margin: 0 100px 50px 200px; }

.intro_text {
  margin-right: 150px; }

html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Lato, sans-serif;
  font-size: 16px;
  color: #1d1e2b;
  max-width: 100%;
  min-width: 700px; }

#root {
  min-height: 100%; }

.logo-path {
  fill: #babcc5; }

.social-media-icons {
  display: flex;
  align-items: center; }
  .social-media-icons .social-media-icon {
    margin: 0 5px;
    height: 28px; }
  .social-media-icons .social-media-email {
    margin-top: 6px; }

h1 {
  font-size: 75px;
  margin: 20px 0; }

